<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">收入-管理费</span>
				</div>
			</div>
			<div class="cash_flow_table table">
				<el-table :data="datalistTotal" border style="width: 100%" empty-text="">
					<el-table-column prop="unit" label="币种" width="100">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column prop="type" label="类型" width="100">
						<template slot-scope="scope">
							收入-管理费
						</template>
					</el-table-column>
					<el-table-column prop="date" label="日期" width="200">
						<template slot-scope="scope">
							<el-date-picker v-model="scope.row.date" @change="getEventCountData($event,scope.$index)" :disabled="scope.$index != currentIndex&&!is_add_page" :editable='false' type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd">
							</el-date-picker>
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="270">
						
					</el-table-column>
					<el-table-column prop="should_pay" label="应缴管理费（万元）" width="180">
					</el-table-column>
					<el-table-column prop="pay" label="实缴管理费（万元）" width="180">
					</el-table-column>
					<el-table-column prop="note" label="备注" width="180">
						<template slot-scope="scope">
							<el-input v-model="scope.row.note" placeholder="请输入内容"  :disabled="scope.$index != currentIndex&&!is_add_page" clearable>
							</el-input>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150" v-if='(!is_add_page) && is_editable'>
						<template slot-scope="scope">
							<el-button v-if="scope.$index != currentIndex" @click="editTotal(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
							<el-button v-if="scope.$index == currentIndex" @click="editSaveTotal(scope.$index,scope.row)" type="text" size="small">保存</el-button>
							<el-button v-if="scope.$index == currentIndex" @click="editCancelTotal(scope.$index,scope.row)" type="text" size="small">取消</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
			</div>
		</div>
		
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">明细</span>
				</div>
			</div>
			<div class="cash_flow_table">
				<div class="table">
					<el-table :data="datalistDetail" border style="width: 100%" empty-text="" sortable>
						<el-table-column prop="id" label="序号" width="100" sortable>
							<template slot-scope="scope">
								{{ scope.$index + 1 }}
							</template>
						</el-table-column>
						<el-table-column prop="name" label="基金名称" width="250" sortable>
						</el-table-column>
						<el-table-column prop="should_pay" label="应缴管理费（万元）" width="180" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.should_pay" v-on:input="calculate" placeholder="请输入内容"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="pay" label="实缴管理费（万元）" width="180" sortable>
							<template slot-scope="scope">
								<el-input v-model.number="scope.row.pay" v-on:input="calculate" placeholder="请输入内容"  :disabled="scope.$index != currentIndexDetail&&!is_add_page" clearable>
								</el-input>
							</template>
						</el-table-column>
						<el-table-column prop="unit" label="币种" sortable>
							<template slot-scope="scope">
								{{ scope.row.unit == 1?'人民币':'美元' }}
							</template>
						</el-table-column>
						<el-table-column prop="date" label="实缴日期" sortable width="200">
							<template slot-scope="scope">
								<el-date-picker v-model="scope.row.date" :disabled="scope.$index != currentIndexDetail&&!is_add_page" :editable='false' type="date" placeholder="选择日期" format="yyyy年MM月dd日" value-format="yyyy-MM-dd">
    								</el-date-picker>
							</template>
						</el-table-column>
						<el-table-column fixed="right" label="操作" width="150" v-if="(!is_add_page) && is_editable">
							<template slot-scope="scope">
								<div class="" v-if="!is_add_page">
									<el-button v-if="scope.$index != currentIndexDetail" @click="editDetail(scope.$index,scope.row)" type="text" size="small">编辑</el-button>
									<el-button v-if="scope.$index == currentIndexDetail" @click="editSaveDetail(scope.$index,scope.row)" type="text" size="small">保存</el-button>
									<el-button v-if="scope.$index == currentIndexDetail" @click="editCancelDetail(scope.$index,scope.row)" type="text" size="small">取消</el-button>
								</div>
								<div class="" v-else>
									<!--<el-button v-if='is_add_page' @click="deleteDetailItem(scope.$index,scope.row)" type="text" size="small">删除</el-button>-->
								</div>
							</template>
						</el-table-column>
						<template slot="empty">
							<div class="table-nodata">
								<img src="../../images/table_nodata.png"> 没有对应数据
							</div>
						</template>
					</el-table>
				</div>
				<div v-if="is_add_page" class="submit_btn_box flex_center">
					<button @click="addCancel" class="hover_btn">取消</button>
					<button @click="saveAddInfo" class="blue_btn">保存</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getCompanyRunCashIncomeManageAddInfoApi,addCompanyRunCashIncomeManageApi,getCompanyRunCashIncomeManageApi,editSaveTotalCompanyRunCashIncomeManageApi,editSaveDetailCompanyRunCashIncomeManageApi,getEventByDateApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn,cash_type_income_manage,event_type_company } from '../../config/mUtils'
	export default {
		inject: ['reload'],
		
		data() {
			return {
				id: '',
				is_add_page: '',//是否是添加页面
				count: '',//查看页面传过来的参数
				is_editable: 1,//汇总页面传过来的是否可编辑字段
				isEditDetailShow: true,
				// 现金流总数据和详情数据开始
				name: '',
				datalistDetail: [],
				datalistTotal: [],
				currentIndex: -1,
				currentIndexDetail: -1,
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.is_editable = this.$route.query.is_editable;
			if (this.$route.query.add) {
				this.is_add_page = this.$route.query.add;
				this.getAddInfo()
			} else {
				this.count = this.$route.query.count;
				this.getInitData()
			}
		},
		computed: {
			
		},
		mounted() {
			this.calculate()
		},
		methods: {
			// 添加功能模块开始
			getAddInfo(){
				getCompanyRunCashIncomeManageAddInfoApi({
					sg_company_manager_id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.datalistDetail = res.data.payload.data;
						this.datalistTotal = res.data.payload.event;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			deleteDetailItem(index,e){
				this.datalistDetail.splice(index,1);
			},
			saveAddInfo(){
				// console.log(this.datalistDetail);
				// console.log(this.datalistTotal);
				let canContinue = true;
				this.datalistDetail.forEach((element,index) => {
					if(element.pay&&element.should_pay&&canContinue){
						if(element.pay>element.should_pay){
							dialogConfirm('info','明细中实缴管理费不能大于应缴管理费!');
							canContinue = false;
						}
					}
				});
				if(!canContinue){
					return false;
				}
				// console.log(1111111);

				// return false;
				let event = JSON.stringify(this.datalistTotal);
				let detail = JSON.stringify(this.datalistDetail);
				
				addCompanyRunCashIncomeManageApi({
					sg_company_manager_id: this.id,
					event: event,
					detail: detail
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.$router.push({
							name: "cashFlowHome",
							query: {
								id: this.id
							}
						})
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addCancel(){
				this.$router.push({
					name: "cashFlowHome",
					query: {
						id: this.id
					}
				})
			},
			// 添加功能模块结束
			
			// 查看功能模块开始
			getInitData(){
				getCompanyRunCashIncomeManageApi({
					sg_company_manager_id: this.id,
					count: this.count,
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						this.datalistTotal = res.data.payload.data.event;
						this.datalistDetail = res.data.payload.data.detail;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			editTotal(index,row){
				console.log(row);
				this.currentIndex = index;
			},
			editCancelTotal(index,row){
				this.currentIndex = -1;
				this.getInitData()
			},
			editSaveTotal(index,row){
				editSaveTotalCompanyRunCashIncomeManageApi({
					id: row.id,
					event: row.event,
					note: row.note,
					unit: row.unit,
					count: row.count,
					date: row.date,
					event_count: row.event_count
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			
			editDetail(index,row){
				console.log(row);
				this.currentIndexDetail = index;
			},
			editCancelDetail(index,row){
				this.currentIndexDetail = -1;
				this.getInitData()
			},
			editSaveDetail(index,row){
				console.log(row);
				editSaveDetailCompanyRunCashIncomeManageApi({
					id: row.id,
					count: row.count,
					fund_id: row.fund_id,
					fund_type: row.fund_type,
					should_pay: row.should_pay,
					pay: row.pay,
					unit: row.unit,
					date: row.date,
				}).then(res => {
					console.log(res);
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						this.reload()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 查看功能模块结束
			// 获取事件字段里的count值
			getEventCountData(date,index){
				if (!date) {
					return
				}
				if (this.is_add_page) {
					var event_id = '';
				} else{
					var event_id = this.datalistTotal[index].id;
				}
				getEventByDateApi({
					type: event_type_company,
					id: this.id,
					cash_type: cash_type_income_manage,
					date: date,
					fund_type: '',
					fund_id: '',
					event_id: event_id
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.datalistTotal[index].event = res.data.payload.event_name;
						this.datalistTotal[index]['event_count'] = res.data.payload.event_count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 计算所有数据的总和
			calculate(){
				var data = this.datalistDetail;
				var totalShouldPay1 = 0;//人民币
				var totalHavePay1 = 0;
				var totalShouldPay2 = 0;//美元
				var totalHavePay2 = 0;
				for (var i=0,max=data.length;i<max;i++) {
					if(!data[i].should_pay){
						data[i].should_pay = 0;
					}
					if(!data[i].pay){
						data[i].pay = 0;
					}
					if (data[i].unit == 1) {
						totalShouldPay1 = Number(totalShouldPay1) + Number(data[i].should_pay);
						totalHavePay1 = Number(totalHavePay1) + Number(data[i].pay);
					} else{
						totalShouldPay2 = Number(totalShouldPay2) + Number(data[i].should_pay);
						totalHavePay2 = Number(totalHavePay2) + Number(data[i].pay);
					}
				}
				if (totalShouldPay1 || totalHavePay1) {
					this.datalistTotal[0].should_pay = totalShouldPay1;
					this.datalistTotal[0].pay = totalHavePay1;
				}
				if (totalShouldPay2 || totalHavePay2) {
					this.datalistTotal[1].should_pay = totalShouldPay2;
					this.datalistTotal[1].pay = totalHavePay2;
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.body {
		padding: 0 0px 30px 0px;
	}
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	.company_name_right .delete {
		margin-right: 60px;
		color: #6292F4;
		font-size: 14px;
	}
	
	.cash_flow_table_title {
		font-size: 14px;
		color: #6292F4;
		margin: 22px 0px;
		.ts {
			color: #FF3B30;
			margin-left: 23px;
		}
		.name {
			color: #6292F4;
		}
	}
	
	.circle_blue {
		width: 7px;
		height: 7px;
		border-radius: 50%;
		margin-right: 5px;
		margin-left: 8px;
		background-color: #6292F4;
	}
	.submit_btn_box{
		margin: 35px 0 20px 0;
	}
	.submit_btn_box button{
		height: 36px;
		margin: 0 10px;
		line-height: 36px;
		padding: 0 25px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
</style>


